import React from 'react';
import Link from 'gatsby-link';
import Helmet from 'react-helmet';

import '../styles/styles.css';
import '../styles/blog-container.css';

import Header from '../components/header';
import Card from "../components/card";
import TechnicalSkills from "../components/techSkills";
import LanguageSkills from "../components/langSkills";
import Timeline from "../components/timeline";
import ContactMe from '../components/contactMe';
import Footer from "../components/footer";


export default function Index({
  data
}) {
  const { edges: posts } = data.allMarkdownRemark;
  return (
    <div>
      <Header />

      {/* ABOUT ME CARD BOX */}        
      <Card/>

      <div className="skills-div" id="skills">
          {/* Technical Skills */}
          <TechnicalSkills/>

          {/* Language Skills */}
          <LanguageSkills />
      </div>

      <Timeline/>      

      {/* BLOG COMPONENT */}
      <div style={{backgroundColor: 'black', paddingTop: '10vh', paddingBottom: '10vh'}}>
        <strong className="card-title" style={{ color:"white", fontWeight: 'bold' }}>
          Blog
        </strong>   
        <div className="blog-posts">
          {posts
            .filter(post => post.node.frontmatter.title.length > 0)
            .map(({ node: post }) => {
              return (
                <div className="blog-post-preview" key={post.id}>
                  <h1>
                    <Link to={post.frontmatter.path}>{post.frontmatter.title}</Link>
                  </h1>
                  <h2>{post.frontmatter.date}</h2>
                  <p>{post.excerpt}</p>
                </div>
              );
            })}
        </div>
      </div>


      <ContactMe/>  

      <Footer/>

  </div>

  );
}

export const pageQuery = graphql`
  query IndexQuery {
    allMarkdownRemark(sort: { order: DESC, fields: [frontmatter___date] }) {
      edges {
        node {
          excerpt(pruneLength: 250)
          id
          frontmatter {
            title
            date(formatString: "MMMM DD, YYYY")
            path
          }
        }
      }
    }
  }
`;